body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/robofriends/static/media/bg.4e7c867f.svg);
  background-color: #09083d;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Quick';
  font-style: normal;
  font-weight: normal;
  src: local('The Quick South St Regular'), url("/robofriends/static/media/The Quick South St.20f6a8eb.woff") format('woff');
  }

  h2 {
    font-family: 'Quick';
    font-size: 2.0rem !important;
    color: #09083d;
  }

  .bg-card {
    background-color: #9eebcfcf;
  }

  p {
    text-transform: lowercase;
  }
@font-face {
  font-family: 'Quick';
  font-style: normal;
  font-weight: normal;
  src: local('The Quick South St Regular'), url("/robofriends/static/media/The Quick South St.20f6a8eb.woff") format('woff');
  }

  h1 {
    font-family: 'Quick';
    font-size: 5.0rem !important;
    color: #FFFFFF;
    background-color: #b600ff;
  }


